import axios from './axios'

// 数据请求中心

// 1.pingTestApi
export function pingTestApi() {
  return axios.request({
    url: '/hello/root',
    method: 'get'
  })
}

export function loginApi(username, password) {
  return axios.request({
    url: '/login',
    method: 'post',
    params: {
      username: username,
      password: password
    }
  })
}

export function logoutApi() {
  return axios.request({
    url: '/logout',
    method: 'post'
  })
}

export function loginCheckApi() {
  return axios.request({
    url: '/check',
    method: 'get'
  })
}

export function getMenuApi() {
  return axios.request({
    url: '/home/menu',
    method: 'get'
  })
}

